import "./game.scss";
import * as React from "react";
import {cs} from "../../../../../../../common/react/chain-services";
import {Grid} from "./grid/grid";
import {UseState} from "../../../../../../../common/react/use-state";
import {equalDeep} from "../../../../../../../common/utils/equal-deep";
import {generateMatrix} from "./generate/generate-matrix";
import {readWord} from "./words/read-word";
import {spc} from "../../../../../../../common/react/state-path-change";
import {conflict} from "./generate/conflict";
import {Load2} from "../../../../../../../common/react/load2";
import {WordList} from "./word-list/word-list";

export const Game = ({size, sentence}) => cs(
    ["wordList", (_, next) => next(toWordList(sentence))],
    ["size", ({wordList}, next) => {
        return next(Math.max(size, ...wordList.map((w) => w.word.length)));
    }],
    ["state", (_, next) => UseState({
        initValue: {key: 1, selecting: null, markeds: []},
        next,
    })],
    ["matrix", ({state, size, wordList}, next) => Load2({
        _key: state.value.key,
        fetch: () => generateMatrix({size: {width: size, height: size}, wordList: wordList.filter((w) => !w.disabled).map((w) => w.word.toLowerCase())}),
        next,
    })],
    ({matrix, state, wordList}) => !matrix.loading && (
        <div className="game-1i1">
            <div className="box">
                <div className="line1">
                    {WordList({
                        wordList: toWordList(sentence),
                        isSelected: (word) => state.value.markeds?.find((m) => m.word === word.toLowerCase()),
                        onHold: (word) => spc(state, ["hintWord"], () => word),
                        onRelease: () => spc(state, ["hintWord"], () => null),
                    })}

                    {Grid1({matrix: matrix.value, state, isValid: (word) => wordList.find((w) => w.word.toLowerCase() === word && !w.disabled)})}
                </div>

                <div className="status">
                    {!matrix.value && (
                        <div className="">
                            Cannot generate matrix
                        </div>
                    )}

                    {!wordList.find((w) => !w.disabled && !state.value.markeds?.find((m) => m.word === w.word.toLowerCase())) && (
                        <div className="congratulations">
                            Congratulations, you finished it !
                        </div>
                    )}
                </div>

                <div className="controls">
                    <button
                        onClick={() => state.onChange({
                            key: state.value.key+1,
                        })}
                    >Reset</button>
                </div>
            </div>

        </div>
    )
);

const toWordList = (sentence) => sentence.replace(/[^a-z ]/ig,"").split(" ").map((word) => ({
    word,
    disabled: word.length === 1,
}));

const Grid1 = ({matrix, state, isValid}) => Grid({
    matrix,

    markedLocations: state.value.markeds?.map((r) => r.location),

    ...!state.value?.selecting ? {
        onMouseDown: (pos) => {
            spc(state, ["selecting"], () => ({start: pos}))
        },
    } : {
        onMouseEnter: (pos) => {
            const {start} = state.value.selecting;
            if (pos.x === start.x && pos.y > start.y) {
                spc(state, ["selecting"], (s) => ({
                    ...s,
                    direction: "vertical",
                    length: pos.y - start.y + 1,
                }));
            } else if (pos.y === start.y && pos.x > start.x) {
                spc(state, ["selecting"], (s) => ({
                    ...s,
                    direction: "horizontal",
                    length: pos.x - start.x + 1,
                }));
            } else {
                spc(state, ["selecting"], (s) => ({start: s.start}));
            }
        },
        onTouchEnd: () => {
            const location = state.value.selecting;
            const word = readWord({matrix, d: location});

            state.change((s) => ({
                ...s,
                selecting: null,
                markeds: word && isValid(word) && !state.value.markeds?.find((m) => m.word === word) ? (
                    [
                        ...(state.value.markeds||[]).filter((m) => !conflict(m.location,location)),
                        {word, location},
                    ]
                ) : (
                    state.value.markeds
                ),
            }));
        },
    },

    getColor: (pos, char) => {
        if (state.value?.selecting) {
            const {start, direction, length} = state.value.selecting;
            if (equalDeep(pos, start)) {
                return "green";
            }

            if (direction === "horizontal" && pos.y === start.y && pos.x > start.x && pos.x - start.x < length) {
                return "green";
            }
            if (direction === "vertical" && pos.x === start.x && pos.y > start.y && pos.y - start.y < length) {
                return "green";
            }
        }

        if (state.value?.hintWord) {
            if (state.value.hintWord[0].toLowerCase() === char) {
                return "yellow";
            }
        }
    },
});